
.EmpireInfo { text-align: center; }

.EmpireInfo__item {
  margin-bottom: 10px;
}

.EmpireInfo__item__input {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 3px;
  width: 400px;
}

.EmpireInfo__item textarea::-webkit-input-placeholder, .EmpireInfo__item textarea {
  font-family: sans-serif;
  font-size: 0.8rem;
}

.EmpireInfo__item textarea::-moz-placeholder, .EmpireInfo__item textarea {
  font-family: sans-serif;
  font-size: 0.8rem;
}

.EmpireInfo__item textarea:-ms-input-placeholder, .EmpireInfo__item textarea {
  font-family: sans-serif;
  font-size: 0.8rem;
}

.EmpireInfo__item textarea::placeholder, .EmpireInfo__item textarea {
  font-family: sans-serif;
  font-size: 0.8rem;
}



